import React, {useState} from 'react';
import './App.css';
import  { Button, Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css'
import {useSpring, animated}  from 'react-spring';
import ReCaptchaV2 from 'react-google-recaptcha';
import Alert from 'react-bootstrap/Alert'

const siteKey = '6Lde484cAAAAAB0cQLFteDPLYY2wAcan1K1fJHFJ';

function Kontakt() {

  const [ime, setIme] = useState('');
  const [poruka, setPoruka] = useState('');
  const [email, setEmail] = useState('');
  const [tempEmail, setTempEmail] = useState('');
  const [showAlert, setShowAlert] = useState(false);
  const [buttonDisabled, setButtonDisabled] = useState(true);
  const [alertPoruka, setAlertPoruka] = useState();

  const fade = useSpring({
    from: {opacity: 0, transform: 'scale(0.5)'},
    to: {opacity: 1, transform: 'scale(1)'},
    config: {
      duration: 1000,
      precision: 0.05,
      clamp: true
    }
  });

  const posaljiPoruku = (e) => {

    if(ime === '' || email === '' || poruka === '') {
      setAlertPoruka('Sva polja moraju biti popunjena!');
      setShowAlert(true);
      return;
    }

    setAlertPoruka();
    fetch('/api/sendEmail', {
      method: "POST",
      headers: {
        "Content-Type" : "application/json"
      },
      body: JSON.stringify({
        ime,
        email,
        poruka
      })
    });

    setShowAlert(true);
    setTempEmail(email);
    setIme(''); setPoruka(''); setEmail('');
  };

  const handleToken = (token) => {
    fetch('/api/captcha', {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        captcha: token
      })
    }).then(res => {
      res.json();
    }).then(data => {
      console.log(data);
    });
  };

  const handleExpire = () => {
    // setForm((currentForm) => {
    //  return {...currentForm, token: null }
    // })
  };

  return (
 
    <animated.div className="kontakt mx-4" style={fade}> 

          <Alert show={showAlert} variant="danger">
                 {alertPoruka ? null : <Alert.Heading>Poruka poslata</Alert.Heading> }
                  <p>               
                  {alertPoruka ? alertPoruka : `Vasa poruka je uspesno poslata, ocekujte odgovor u sto kracem roku na ${tempEmail}`}
                  </p>
          </Alert>
          
          <span className="mt-2"> </span>   
      <span>Ukoliko imate bilo kakvih pitanja mozete me kontaktirati: </span>
      <span> </span>
      <span style={{color: "red"}}>evolutionphotokv@gmail.com </span>
      <span style={{color: "red"}}>Telefon: 060/375-0-661</span>
      <span> </span>
      <span >Ili putem kontakt forme</span>
      <span> </span>
    <div className="kontaktForma">
      <input className="form-control kontaktIme no-border" value={ime} type="text" id="textIme" placeholder="Ime" onChange={(e)=> setIme(e.target.value)}/>
      <span> </span>
      <input className="form-control kontaktIme no-border" value={email} type="email" id="textEmail" placeholder="Email" onChange={(e)=> setEmail(e.target.value)}/>
      <span> </span>
      <textarea className="form-control no-border" id="textPoruka" rows="5" value={poruka} placeholder="Vasa poruka" onChange={(e)=> setPoruka(e.target.value)}/>
      <span> </span>
      <button type="button" disabled={buttonDisabled} class="btn btn-danger mt-3 no-border" onClick={posaljiPoruku}>Posalji</button>
    </div>

    <ReCaptchaV2 sitekey={siteKey}
                 onChange={handleToken}
                 onExpire={handleExpire}/>
    </animated.div>

  );
}

export default Kontakt;